$(document).ready(() => {
    const enteries = [
        { label: 'HTML' },
        { label: 'CSS' },
        { label: 'SCSS' },
        { label: 'REST' },
        { label: 'GSAP' },
        { label: 'TypeScript' },
        { label: 'JavaScript' },
        { label: 'pug' },
        { label: 'Expressjs' },
        { label: 'Node JS' },
        { label: 'Git' },
        { label: 'C#' },
        { label: 'C++' },
        { label: 'Discord.js' },
    ];

    const settings = {
        entries: enteries,
        width: 700,
        height: 700,
        bgDraw: false,
        opacityOut: 0.15,
        opacitySpeed: 6,
        fov: 800,
        radius: '80%',
        radiusMin: 75,
        speed: 2,
        fontFamily: 'Open Sans',
        fontSize: 30,
        fontColor: '#08fdd8',
    }

    new SVG3DTagCloud(document.getElementById('svg3dtagcloud'), settings);
})